<script>
import qs from "qs";

export default {
  props:['viewData'],
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      process: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"],
      loading:false,
    };
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("childFn", function(item) {
      this.dialog = true;
      this.enterEdit(item);
    });
  },

  methods: {
    enterAdd() {
      //執行新增流程
      this.process = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";

      if (this.viewData.length > 0){
        this.list = {...this.viewData[0]};
        this.list.date = '';
      }else{
        this.list = {};
      }
    },
    enterEdit(item) {
      //執行修改流程
      this.process = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //暫存未修改前資料
      this.list = { ...item }; //列表中複製一份顯示及修改用資料
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        var postData = {
          datalist: JSON.stringify(this.list),
          u_snkey: this.$store.state.uData.snkey,
          date: this.list.date,
          create_man:
            this.$store.state.pData.username +
            "(" +
            this.$store.state.time +
            ")" //執行動作的人,時間,內容
        };

        this.loading = true;
        this.axios
          .post(
            "general/add/" +
              this.$store.state.databaseName +
              "/sociopsychological",
            qs.stringify(postData)
          )
          .then(rs => {
            // console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        var postData = {
          snkey : this.list.snkey,
          datalist: JSON.stringify(this.list),
          edit_man:
            this.$store.state.pData.username +
            "(" +
            this.$store.state.time +
            ")" //執行動作的人,時間,內容
        };

        this.loading = true;
        this.axios
          .post(
            "general/edit/" +
              this.$store.state.databaseName +
              "/sociopsychological",
            qs.stringify(postData)
          )
          .then(rs => {
            console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              for (var i in this.list) {
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    }
  }
};
</script>


